.contact-preview {
    display: inline-block;
    padding: 0 25px;
    height: 50px;
    width: 240px;
    font-size: 17px;
    line-height: 50px;
    border-radius: 25px;
    background-color: #f1f1f1; 
}
    
.contact-preview img {
    float: left;
    margin: 0 10px 0 -25px;
    height: 50px;
    width: 50px;
    border-radius: 50%; 
}  