body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.moves-list {
    background-color: lightgray;
    margin: 15px;
    text-align: left;
    padding: 15px;
}

.moves-title {
    margin: 0;
}

.moves-hr {
    margin: 10px 0;
}

.moves-to {
    margin-bottom: 8px;
}
.user-details {
    padding: 20px;
    margin: 0 0 20px 0;
}
  
.user-name {
    margin: 0 0 20px 0;
    font-size: 22px;
}
  
.user-coins-count {
    margin: 10px 0 0 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
  
.user-coins-rate {
    margin: 10px 0 0 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
  
.user-coins-count img, .user-coins-rate img {
    margin: 0 5px 0 0;
}
.chart {
    margin: 20px 0;
}
  
.chart-name {
    margin: 5px 0;
}
  
.chart-desc {
    font-size: 0.8rem;
}
.statistic-chart:not(:last-child) {
    margin: 0 0 20px 0;
    border-bottom: 1px solid #f4f5f7;
}
.contact-preview {
    display: inline-block;
    padding: 0 25px;
    height: 50px;
    width: 240px;
    font-size: 17px;
    line-height: 50px;
    border-radius: 25px;
    background-color: #f1f1f1; 
}
    
.contact-preview img {
    float: left;
    margin: 0 10px 0 -25px;
    height: 50px;
    width: 50px;
    border-radius: 50%; 
}  
.contacts-list-item {
    margin: 10px 0; 
}

.add-contact-btn {
    position: fixed;
    bottom: 10px; 
    right: 10px;
}

.add-contact-btn:hover {
    cursor: pointer;
}
.search-bar {
    width: 250px;
    height: 30px;
    border-radius: 5px;
    padding: 5px;
    border: 1px solid black;
    margin-top: 10px;
}

.search-bar:focus {
    background-color: lightgrey;
}
.contacts-page {
    position: relative;
    height: 100%; 
  }
  
  .contacts-page .search-container {
    height: 40px;
    padding: 0 10px; 
  }
  
  /* .contacts-page .contacts-container {
    max-height: 360px;
    overflow-x: auto; 
  } */
  
  .contacts-page .action-container {
    position: absolute;
    bottom: -3px;
    right: 10px;
  }
.transfer-fund {
    background-color: lightblue;
    margin: 15px;
    text-align: left;
    padding: 15px;
}

.transfer-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 15px;
}

.transfer-input {
    margin: 0 10px;
    width: 100px;
}
.contact-details-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 40px; 
}
.contact-details-header a:not(:last-child) {
    margin: 0 10px 0 0; 
}
  
.contact-details .contact-details-row {
    margin: 10px 0 0 0; 
}

.contact-details-body {
    text-align: left;
    margin-left: 15px;
}

.details-nav {
    margin: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
  
.moves-list-container, .transter-coins-container {
    margin: 20px 0;
}
.edit-nav {
    margin: 15px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

.back-btn {
    cursor: pointer;
}

.input-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 15px;
}

.input {
    padding: 5px;
    border-radius: 5px;
    width: 65vw;
}

.btn {
    margin: 15px;
    float: right;
}
.signup-page {
    height: 60vh;
    width: 100%;
    margin-top: 10px;    
}

.signup-form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}
.main-nav {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
}
  
.nav-img {
    height: 40px;
    width: 40px;
    padding: 5px;
}
  
.nav-link:active, .nav-link:hover {
    background-color: lightblue;
}
  
.activeRoute {
    background-color: lightblue;
}
body {
  box-sizing: border-box;
}

.App {
  text-align: center;
}

ul {
  list-style-type: none;
  -webkit-padding-start: 0;
          padding-inline-start: 0;
  -webkit-margin-after: 0;
          margin-block-end: 0;
  -webkit-margin-before: 0;
          margin-block-start: 0;
}

hr {
  margin: 0 20px;
}
