.contact-details-header {
    display: flex;
    justify-content: space-between;
    height: 40px; 
}
.contact-details-header a:not(:last-child) {
    margin: 0 10px 0 0; 
}
  
.contact-details .contact-details-row {
    margin: 10px 0 0 0; 
}

.contact-details-body {
    text-align: left;
    margin-left: 15px;
}

.details-nav {
    margin: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
  
.moves-list-container, .transter-coins-container {
    margin: 20px 0;
}